.language-selector {
  display: flex;
  align-items: center;
  gap: 6px;
}

.globe-icon {
  font-size: 18px;
}

.language-selector select {
  background: white;
  color: #000;
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 5px 8px;
  font-size: 13px;
  cursor: pointer;
}
